<template>

     <v-layout align-start>
         <v-flex>
            <v-card>
                <v-card-title>
                        <v-flex xs12 sm2 md2 lg2 text-xs-center>
                            <h2>Consulta Ventas</h2>                             
                        </v-flex>
                        <v-divider
                    vertical
                    ></v-divider>
                   
                    <!-- Datepicker -->
                        <v-flex xs8 sm3 md2 text-xs-center v-if="verNuevo==0">
                            <v-menu
                            v-model="menu1"
                            full-width
                            max-width="290"
                            lazy
                            transition="scale-transition"
                            :close-on-content-click="false"
                            >
                                <template v-slot:activator="{ on }">
                                    <v-text-field 
                                     readonly
                                     class="text-xs-center" 
                                     label="Desde:" 
                                     prepend-icon="event" 
                                     clearable 
                                     clear-icon="highlight_off" 
                                     v-on="on"
                                     :value="fechaInicioFormateada"
                                    @click:clear="fechaInicio=''"></v-text-field>
                                </template>
                                <v-date-picker
                                v-model="fechaInicio"
                                locale="es"
                                @change="menu1 = false"
                                ></v-date-picker>
                            </v-menu>
                        </v-flex>
                    <v-flex xs8 sm3 md2 text-xs-center v-if="verNuevo==0">
                        <v-menu
                            v-model="menu2"
                            full-width
                            max-width="290"
                            lazy
                            transition="scale-transition"
                            :close-on-content-click="false"
                            >
                             <template v-slot:activator="{ on }">
                                <v-text-field 
                                readonly
                                class="text-xs-center" 
                                label="Hasta:"  
                                prepend-icon="event" 
                                clearable 
                                clear-icon="highlight_off" 
                                v-on="on" 
                                :value="fechaFinFormateada"
                                @click:clear="fechaFin=''"></v-text-field>
                             </template>
                            <v-date-picker
                                v-model="fechaFin"
                                locale="es"
                                @change="menu2 = false"
                                ></v-date-picker>    
                        </v-menu> 
                    </v-flex>
                    
                    <v-flex xs12 sm3 text-xs-center>
                    <v-btn v-if="verNuevo==0" @click="listar();loadingBtnBuscar=true" :loading="loadingBtnBuscar" color="primary" dark >Buscar</v-btn> 
                    </v-flex>
                </v-card-title>

                 <!-- Dialog para imprimir Comprobante de Venta -->
                    <v-dialog v-model="comprobanteModal" max-width="1000px">
                        <v-card>
                            <v-card-text>
                                <v-btn @click="crearPDF()"><v-icon>print</v-icon></v-btn>
                                <div id="factura">
                                     <header>
                                        <div id="logo">
                                            <img src="data:image/png;base64," id="imagen">
                                        </div>
                                        <div id="datos">
                                            <p id="encabezado">
                                                <b>CrisssIT</b><br>San Martn 1368, Godoy Cruz - Mendoza, Argentina<br>Telefono:(+54)456742904<br>Email:criss.sarmiento23@gmail.com
                                            </p>
                                        </div>
                                        <div id="fact">
                                            <p>{{tipo_comprobante}}<br>
                                            {{serie_comprobante}}-{{num_comprobante}}<br>
                                            {{fecha_hora}}</p>
                                        </div>
                                    </header>
                                    <br>
                                    <section>
                                        <div>
                                            <table id="facliente">
                                                <tbody>
                                                    <tr>
                                                        <td id="cliente">
                                                            <strong>Sr(a). {{cliente}}</strong><br>
                                                            <strong>Documento:</strong> {{num_documento}}<br>
                                                            <strong>Direccin:</strong> {{direccion}}<br>
                                                            <strong>Telfono:</strong> {{telefono}}<br>
                                                            <strong>Email:</strong> {{email}}
                                                        </td>
                                                    </tr>
                                                </tbody>
                                            </table>
                                        </div>
                                    </section>
                                    <br>
                                    <section>
                                        <div>
                                            <table id="facarticulo">
                                                <thead>
                                                    <tr id="fa">
                                                        <th>CANT</th>
                                                        <th>DESCRIPCION</th>
                                                        <th>PRECIO UNIT</th>
                                                        <th>DESC.</th>
                                                        <th>PRECIO TOTAL</th>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    <tr v-for="det in detalles" :key="det.iddetalle_venta">
                                                        <td style="text-align:center;">{{det.cantidad}}</td>
                                                        <td>{{det.articulo}}</td>
                                                        <td style="text-align:right;">{{det.precio.toFixed(2)}}</td>
                                                        <td style="text-align:right;">{{det.descuento.toFixed(2)}}</td>
                                                        <td style="text-align:right;">{{(det.cantidad*det.precio-det.descuento).toFixed(2)}}</td>
                                                    </tr>
                                                </tbody>
                                                <tfoot>
                                                    <tr>
                                                        <th></th>
                                                        <th></th>
                                                        <th></th>
                                                        <th style="text-align:right;">SUBTOTAL</th>
                                                        <th style="text-align:right;">$ {{totalParcial=(total-totalImpuesto).toFixed(2)}}</th>
                                                    </tr>
                                                    <tr>
                                                        <th></th>
                                                        <th></th>
                                                        <th></th>
                                                        <th style="text-align:right;">IGV ({{impuesto}}%)</th>
                                                        <th style="text-align:right;">$ {{totalImpuesto=((total*impuesto)/(100+impuesto)).toFixed(2)}}</th>
                                                    </tr>
                                                    <tr>
                                                        <th></th>
                                                        <th></th>
                                                        <th></th>
                                                        <th style="text-align:right;">TOTAL</th>
                                                        <th style="text-align:right;">$ {{total=(calcularTotal).toFixed(2)}}</th>
                                                    </tr>
                                                </tfoot>
                                            </table>
                                        </div>
                                    </section>
                                    <br>
                                    <br>
                                    <footer>
                                        <div id="gracias">
                                            <p><b>Gracias por su compra!</b></p>
                                        </div>
                                    </footer>
                                </div>
                                <v-btn @click="ocultarComprobante()" color="blue darken-1" flat>Cancelar</v-btn>
                            </v-card-text>
                        </v-card>
                    </v-dialog>

                <v-data-table
                :headers="headers"
                :items="ventas"
                :loading="loading"
                :pagination.sync="pagination"
                class="elevation-1"
                v-if="verNuevo==0"
               
            >
                <template v-slot:items="props">
                    <td class="justify-center layout px-0">
                        <v-icon
                        small
                        class="mr-2"
                        @click="verDetalles(props.item)"
                        >
                        tab
                        </v-icon>
                        <v-icon
                        small
                        class="mr-2"
                        @click="mostrarComprobante(props.item)"
                        >
                        print
                        </v-icon>
                    </td>
                    <td>{{ props.item.usuario }}</td>
                    <td>{{ props.item.cliente }}</td>
                    <td>{{ props.item.tipo_comprobante }}</td>
                    <td>{{ props.item.serie_comprobante }}</td>
                    <td>{{ props.item.num_comprobante }}</td>
                    <td>{{ props.item.fecha_hora }}</td>
                    <td>%{{ props.item.impuesto }}</td>
                    <td>${{ props.item.total }}</td>
                    <td>
                        <div v-if="props.item.estado=='Aceptado'">
                            <span class="blue--text">Aceptado</span>
                        </div>
                        <div v-else>
                            <span class="red--text">{{props.item.estado}}</span>
                        </div>
                    </td>
                
                </template>
                <template v-slot:no-data>
                <v-btn color="primary" @click="listar">Resetear</v-btn>
                </template>
            </v-data-table>
            <v-slide-y-transition mode="out-in">
            <template>
            <v-container grid-list-sm class="pa-4 white" v-if="verNuevo">
                <v-form ref="form">
                <v-layout row wrap >
                    
                        <v-flex xs12 sm4 md4 lg4 xl4>
                            <v-select v-model="tipo_comprobante"
                            :items="comprobantes" label="Tipo Comprobante" :rules="[v => !!v || 'Seleccione un Tipo Comprobante']" readonly>
                            </v-select>
                        </v-flex>
                        <v-flex xs12 sm4 md4 lg4 xl4>
                            <v-text-field v-model="serie_comprobante" label="Serie Comprobante" :rules="inputRules" readonly>
                            </v-text-field>
                        </v-flex>
                        <v-flex xs12 sm4 md4 lg4 xl4>
                            <v-text-field v-model="num_comprobante" label="Nmero Comprobante" :rules="inputRules" readonly>
                            </v-text-field>
                        </v-flex>
                        <v-flex xs12 sm8 md8 lg8 xl8>
                            <v-select v-model="idcliente"
                            :items="clientes" label="Clientes" :rules="[v => !!v || 'Seleccione un Cliente']" readonly>
                            </v-select>
                        </v-flex>
                        <v-flex xs12 sm4 md4 lg4 xl4>
                            <v-text-field type="number" v-model="impuesto" label="Impuesto" 
                            :rules="[v => !!v || 'El campo es requerido',
                                     v => v>0 ||'Ingrese un impuesto vlido']" readonly>
                            </v-text-field>
                        </v-flex>
                        
                    <v-flex xs12 sm2 md2 lg2 xl2 v-if="errorArticulo">
                        <div class="red--text" v-text="errorArticulo">
                        </div>
                    </v-flex>
                    <v-flex xs12 sm12 md12 lg12 xl12>
                        <v-data-table
                            :headers="cabeceraDetalles"
                            :items="detalles"
                            :loading="loading"
                            hide-actions
                            class="elevation-1"
                        >
                            <template v-slot:items="props">
                                <td>{{ props.item.articulo }}</td>
                                <td><v-text-field type="number" v-model="props.item.cantidad" readonly></v-text-field></td>
                                <td><v-text-field type="number" prefix="$" v-model="props.item.precio" readonly></v-text-field></td>
                                <td><v-text-field type="number" prefix="$" v-model="props.item.descuento" readonly></v-text-field></td>
                                <td>$ {{ props.item.cantidad * props.item.precio -props.item.descuento}}</td>
                            </template>
                            <template v-slot:no-data>
                                <h3>No hay artculos agregados al detalle</h3>
                            </template>
                        </v-data-table>
                        <v-flex class="text-xs-right">
                            <strong>Total Parcial: </strong>$ {{totalParcial=(total-totalImpuesto).toFixed(2)}}
                        </v-flex>
                        <v-flex class="text-xs-right">
                            <strong>Total Impuesto: </strong>$ {{totalImpuesto=((total*impuesto)/(100+impuesto)).toFixed(2)}}
                        </v-flex>
                        <v-flex class="text-xs-right">
                            <strong>Total Neto: </strong>$ {{total=(calcularTotal).toFixed(2)}}
                        </v-flex>
                    </v-flex>
                    <v-flex xs12 sm12 md12 v-show="valida">
                        <div class="red--text" v-for="v in validaMensaje" :key="v" v-text="v">
                        </div>
                     </v-flex>
                    <v-flex xs12 sm12 md12 lg12 xl12>
                        <v-btn @click="ocultarNuevo" color="blue darken-1" flat>Volver</v-btn>
                        <v-btn v-if="verDet==0" @click="guardar" color="success">Guardar</v-btn>
                    </v-flex>
                </v-layout>
                </v-form>
            </v-container>
            </template>
            </v-slide-y-transition>
            </v-card>
         </v-flex>
     </v-layout>


</template>

<script>
    import axios from 'axios'
    import jsPDF from 'jspdf'
    import html2canvas from 'html2canvas'
    import moment from 'moment'
    export default {
        data(){
            return{

                pagination: {
                    sortBy: 'fecha_hora',
                    descending: true
                },

                ventas:[],
                dialog: false,
                headers: [
                { text: 'Opciones', value: 'opciones', sortable: false },
                { text: 'Usuario', value: 'usuario' },
                { text: 'Cliente', value: 'cliente' },
                { text: 'Tipo Comprobante', value: 'tipo_comprobante' },
                { text: 'Serie Comprobante', value: 'serie_comprobante',sortable: false},
                { text: 'Nmero Comprobante', value: 'num_comprobante',sortable: false},
                { text: 'Fecha', value: 'fecha_hora'},
                { text: 'Impuesto', value: 'impuesto',sortable: false},
                { text: 'Total', value: 'total',sortable: false},
                { text: 'Estado', value: 'estado', sortable: false }
                
                ],
                cabeceraDetalles: [
                { text: 'Artculo', value: 'articulo', sortable: false },
                { text: 'Cantidad', value: 'cantidad',sortable: false},
                { text: 'Precio', value: 'precio',sortable: false},
                { text: 'Descuento', value: 'descuento',sortable: false},
                { text: 'Subtotal', value: 'subtotal', sortable: false }
                ],

                cabeceraArticulos:[
                    { text: 'Seleccionar', value: 'seleccionar', sortable: false },
                    { text: 'Artculo', value: 'articulo' },
                    { text: 'Categora', value: 'categoria'},
                    { text: 'Descripcin', value: 'descripcio',sortable: false},
                    { text: 'Stock', value: 'stock', sortable: false },
                    { text: 'Precio Venta', value: 'precio_venta',sortable: false},

                ],

                detalles:[],

                search: '',
                loading: true,
                loadingBtnBuscar: false,
                id: '',
                idcliente:'',
                clientes:[],
                tipo_comprobante:'',
                comprobantes:['FACTURA','BOLETA','TICKET'],
                serie_comprobante: '',
                num_comprobante: '',
                impuesto: 18,
                codigo:'',
                verNuevo:0,
                errorArticulo: null,
                totalParcial:0,
                totalImpuesto:0,
                total:0,
                articulos:[],
                texto:'',
                verArticulos:0,
                verDet:0,
                valida: 0,
                validaMensaje:[],
                adModal: 0,
                adAccion: 0,
                adNombre: '',
                adId: '',
                inputRules:[
                     v => !!v || 'El campo es requerido',
                ],
                //Variables para imprimir Comprobante factura
                comprobanteModal:0,
                cliente:'',
                fecha_hora:'',
                num_documento:'',
                direccion:'',
                telefono:'',
                email:'',
                //Variables para datepickers
                menu1: false,
                menu2: false,
                fechaInicio: '',
                fechaFin:'',

            }

        },
            computed: {
                calcularTotal:function(){
                    var resultado=0.0;
                    for (var i =0;i <this.detalles.length; i++) {
                        resultado=resultado+(this.detalles[i].precio*this.detalles[i].cantidad-this.detalles[i].descuento);
                    }
                    return resultado;
                },

                  fechaInicioFormateada () {
                    return this.fechaInicio ? moment(this.fechaInicio).format('DD/MM/YYYY'):''
                },

                
                  fechaFinFormateada () {
                    return this.fechaFin ? moment(this.fechaFin).format('DD/MM/YYYY'):''
                }

            },

            watch: {
                dialog (val) {
                val || this.close()
                }
            },

            created () {
                this.listar();
                this.select();
            },
        methods:{
           
            crearPDF(){
                var quotes= document.getElementById('factura');
                html2canvas(quotes).then(function(canvas) {
                    var imgData= canvas.toDataURL('image/png');
                    var imgWidth = 210;
                    var pageHeight=295;
                    var imgHeight = canvas.height * imgWidth/ canvas.width;
                    var heightLeft = imgHeight;
                    var doc= new jsPDF('p', 'mm','a4');
                    var position=0;

                    doc.addImage(imgData, 'PNG',0,position,imgWidth,imgHeight);
                    doc.save('ComprobanteVenta.pdf');
                });
            },

            mostrarComprobante(item){
                this.limpiar();
                this.tipo_comprobante=item.tipo_comprobante;
                this.serie_comprobante=item.serie_comprobante;
                this.num_comprobante=item.num_comprobante;
                this.cliente=item.cliente;
                this.num_documento=item.num_documento;
                this.direccion=item.direccion;
                this.telefono=item.telefono;
                this.email=item.email;
                this.fecha_hora=item.fecha_hora;
                this.impuesto=item.impuesto;
                this.listarDetalles(item.idventa);
                this.comprobanteModal=1
            },
            ocultarComprobante(){
                this.comprobanteModal=0;
                this.limpiar();
            },
            mostrarNuevo(){
                this.verNuevo=1;
            },
            ocultarNuevo(){
                this.verNuevo=0;
                this.limpiar();
            },

           

            agregarDetalle(data=[]){
                this.errorArticulo=null;
                if (this.encuentra(data['idarticulo'])) {
                    this.errorArticulo="El artculo ya ha sido agregado";
                } else {
                    this.detalles.push(
                    {idarticulo:data['idarticulo'],
                    articulo: data['nombre'],
                    cantidad:1,
                    precio:data['precio_venta'],
                    descuento:0}
                  );
                }
                
            },

            encuentra(id){
                var sw=0;
                for (var i = 0; i<this.detalles.length; i++) {
                    if (this.detalles[i].idarticulo==id) {
                        sw=1;
                    }
                }
                return sw;
            },

            listar(){
                let me=this;
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion={headers : header};
                let url='';
                if (!me.fechaInicio || !me.fechaFin) {
                    url='api/Ventas/Listar';
                } else {
                    url='api/Ventas/ConsultaFechas/'+me.fechaInicio+'/'+me.fechaFin;
                }
                axios.get(url,configuracion).then(function(response){
                    //console.log(response);
                    me.ventas=response.data;
                    //Formateo de fecha y hora
                    me.ventas.map(function(x){
                        x.fecha_hora=moment(x.fecha_hora).format('DD/MM/YYYY HH:mm');
                    });
                    me.loading=false;
                    me.loadingBtnBuscar=false;

                }).catch(function(error){
                    console.log(error);
                });

            },

            
            listarDetalles(id){
                let me=this;
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion={headers : header};
                axios.get('api/Ventas/ListarDetalles/'+id,configuracion).then(function(response){
                    //console.log(response);
                    me.detalles=response.data;
                    me.loading=false;

                }).catch(function(error){
                    console.log(error);
                });

            },

            verDetalles(item){
                this.limpiar();
                this.tipo_comprobante=item.tipo_comprobante;
                this.serie_comprobante=item.serie_comprobante;
                this.num_comprobante=item.num_comprobante;
                this.idcliente=item.idcliente;
                this.impuesto=item.impuesto;
                this.listarDetalles(item.idventa);
                this.verNuevo=1;
                this.verDet=1;
            },

            select(){
                let me=this;
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion={headers : header};
                var clientesArray=[];
                axios.get('api/Personas/SelectClientes',configuracion).then(function(response){
                    clientesArray=response.data;
                    clientesArray.map(function(x){
                        me.clientes.push({text: x.nombre, value:x.idpersona});
                    });

                }).catch(function(error){
                    console.log(error);
                });

            },

            limpiar(){
                this.id="";
                this.idcliente="";
                this.tipo_comprobante="";
                this.serie_comprobante="";
                this.num_comprobante="";
                this.impuesto="18";
                this.codigo="";
                this.detalles=[];
                this.total=0;
                this.totalImpuesto=0
                this.totalParcial=0;
                this.verDet=0;
                this.validaMensaje=[];
            },

            
      

        }
            
    }
</script>
<style>
    #factura {
        padding: 20px;
        font-family: Arial, sans-serif;
        font-size: 16px ;
    }

    #logo {
        float: left;
        margin-left: 2%;
        margin-right: 2%;
    }
    #imagen {
        width: 100px;
    }

    #fact {
        font-size: 18px;
        font-weight: bold;
        text-align: center;
    }   

    #datos {
        float: left;
        margin-top: 0%;
        margin-left: 2%;
        margin-right: 2%;
        /*text-align: justify;*/
    }

    #encabezado {
        text-align: center;
        margin-left: 10px;
        margin-right: 10px;
        font-size: 16px;
    }

    section {
        clear: left;
    }

    #cliente {
        text-align: left;
    }

    #facliente {
        width: 40%;
        border-collapse: collapse;
        border-spacing: 0;
        margin-bottom: 15px;
    }

    #fa {
        color: #FFFFFF;
        font-size: 14px;
    }

    #facarticulo {
        width: 100%;
        border-collapse: collapse;
        border-spacing: 0;
        padding: 20px;
        margin-bottom: 15px;
    }

    #facarticulo thead {
        padding: 20px;
        background: #2183E3;
        text-align: center;
        border-bottom: 1px solid #FFFFFF;
    }

    #gracias {
        text-align: center;
    }

</style>

