<template>
    <v-layout align-start>
        <v-flex>
            <v-toolbar flat color="white">
                <v-toolbar-title>Ventas</v-toolbar-title>
                    <v-divider
                    class="mx-2"
                    inset
                    vertical
                    ></v-divider>
                    <v-spacer></v-spacer>
                    <v-text-field v-if="verNuevo==0" class="text-xs-center" v-model="search" append-icon="search" label="Bsqueda" single-line hide-details></v-text-field>
                    <!--<v-btn v-if="verNuevo==0" @click="listar()" color="primary" dark class="mb-2">Buscar</v-btn>-->
                    <v-spacer></v-spacer>
                    <v-btn v-if="verNuevo==0" @click="mostrarNuevo" color="primary" dark class="mb-2">Nuevo</v-btn>
                    <v-dialog v-model="verArticulos" max-width="1000px">
                        <v-card>
                            <v-card-title>
                                <span class="headline">Seleccione un artculo</span>
                            </v-card-title>
                            <v-card-text>
                                <v-container grid-list-md>
                                    <v-layout wrap>
                                        <v-flex xs12 sm12 md12 lg12 xl12>
                                            <v-text-field append-icon="search" class="text-xs-center" v-model="texto" label="Ingrese artculo a buscar" @keyup="listarArticulo()">

                                            </v-text-field>
                                            <template>
                                                 <v-data-table
                                                    :headers="cabeceraArticulos"
                                                    :items="articulos"
                                                    :loading="loading"
                                                    class="elevation-1"
                                                >
                                                    <template v-slot:items="props">
                                                        <td class="justify-center layout px-0">
                                                            <v-icon
                                                            small
                                                            class="mr-2"
                                                            @click="agregarDetalle(props.item)"
                                                            >
                                                            add
                                                            </v-icon>
                                                        </td>
                                                        <td>{{ props.item.nombre }}</td>
                                                        <td>{{props.item.categoria}}</td>
                                                        <td>{{props.item.descripcion}}</td>
                                                        <td>{{props.item.stock}}</td>
                                                        <td>{{props.item.precio_venta}}</td>
                                                    </template>
                                                    <template v-slot:no-data>
                                                        <h3>No hay artculos para mostrar</h3>
                                                    </template>
                                                </v-data-table>
                                            </template>
                                        </v-flex>
                                    </v-layout>
                                </v-container>
                            </v-card-text>
                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn @click="ocultarArticulos()" color="blue darken" flat>
                                    Cancelar
                                </v-btn>
                            </v-card-actions>
                        </v-card>
                    </v-dialog>
                    <v-dialog v-model="adModal" max-width="290">
                        <v-card>
                            <v-card-title class="headline"  v-if="adAccion==1">Activar Item?</v-card-title>
                            <v-card-title class="headline"  v-if="adAccion==2">Anular Venta?</v-card-title>
                            <v-card-text>
                                Ests a punto de
                                <span v-if="adAccion==1">Activar</span>
                                <span v-if="adAccion==2">Anular</span>
                                el tem {{adNombre}}
                            </v-card-text>
                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn color="green darken-1" flat="flat" @click="activarDesactivarCerrar">
                                    Cancelar
                                </v-btn>
                                <v-btn v-if="adAccion==1" color="orange darken-4" flat="flat" @click="activar">
                                    Activar
                                </v-btn>
                                 <v-btn v-if="adAccion==2" color="orange darken-4" flat="flat" @click="desactivar">
                                    Anular
                                </v-btn>
                            </v-card-actions>
                        </v-card>
                    </v-dialog>

                    <!-- Dialog para imprimir Comprobante de Venta -->
                    <v-dialog v-model="comprobanteModal" max-width="1000px">
                        <v-card>
                            <v-card-text>
                                <v-btn @click="crearPDF()"><v-icon>print</v-icon></v-btn>
                                <div id="factura">
                                     <header>
                                        <div id="logo">
                                            <img src="data:image/png;base64," id="imagen">
                                        </div>
                                        <div id="datos">
                                            <p id="encabezado">
                                                <b>CrisssIT</b><br>San Martn 1368, Godoy Cruz - Mendoza, Argentina<br>Telefono:(+54)456742904<br>Email:criss.sarmiento23@gmail.com
                                            </p>
                                        </div>
                                        <div id="fact">
                                            <p>{{tipo_comprobante}}<br>
                                            {{serie_comprobante}}-{{num_comprobante}}<br>
                                            {{fecha_hora}}</p>
                                        </div>
                                    </header>
                                    <br>
                                    <section>
                                        <div>
                                            <table id="facliente">
                                                <tbody>
                                                    <tr>
                                                        <td id="cliente">
                                                            <strong>Sr(a). {{cliente}}</strong><br>
                                                            <strong>Documento:</strong> {{num_documento}}<br>
                                                            <strong>Direccin:</strong> {{direccion}}<br>
                                                            <strong>Telfono:</strong> {{telefono}}<br>
                                                            <strong>Email:</strong> {{email}}
                                                        </td>
                                                    </tr>
                                                </tbody>
                                            </table>
                                        </div>
                                    </section>
                                    <br>
                                    <section>
                                        <div>
                                            <table id="facarticulo">
                                                <thead>
                                                    <tr id="fa">
                                                        <th>CANT</th>
                                                        <th>DESCRIPCION</th>
                                                        <th>PRECIO UNIT</th>
                                                        <th>DESC.</th>
                                                        <th>PRECIO TOTAL</th>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    <tr v-for="det in detalles" :key="det.iddetalle_venta">
                                                        <td style="text-align:center;">{{Number(det.cantidad)}}</td>
                                                        <td>{{det.articulo}}</td>
                                                        <td style="text-align:right;">{{Number(det.precio).toFixed(2)}}</td>
                                                        <td style="text-align:right;">{{Number(det.descuento).toFixed(2)}}</td>
                                                        <td style="text-align:right;">{{Number(det.cantidad*det.precio-det.descuento).toFixed(2)}}</td>
                                                    </tr>
                                                </tbody>
                                                <tfoot>
                                                    <tr>
                                                        <th></th>
                                                        <th></th>
                                                        <th></th>
                                                        <th style="text-align:right;">SUBTOTAL</th>
                                                        <th style="text-align:right;">$ {{totalParcial=(total-totalImpuesto).toFixed(2)}}</th>
                                                    </tr>
                                                    <tr>
                                                        <th></th>
                                                        <th></th>
                                                        <th></th>
                                                        <th style="text-align:right;">IGV ({{impuesto}}%)</th>
                                                        <th style="text-align:right;">$ {{totalImpuesto=((total*impuesto)/(100+impuesto)).toFixed(2)}}</th>
                                                    </tr>
                                                    <tr>
                                                        <th></th>
                                                        <th></th>
                                                        <th></th>
                                                        <th style="text-align:right;">TOTAL</th>
                                                        <th style="text-align:right;">$ {{total=(calcularTotal).toFixed(2)}}</th>
                                                    </tr>
                                                </tfoot>
                                            </table>
                                        </div>
                                    </section>
                                    <br>
                                    <br>
                                    <footer>
                                        <div id="gracias">
                                            <p><b>Gracias por su compra!</b></p>
                                        </div>
                                    </footer>
                                </div>
                                <v-btn @click="ocultarComprobante()" color="blue darken-1" flat>Cancelar</v-btn>
                            </v-card-text>
                        </v-card>
                    </v-dialog>

            </v-toolbar>
            <v-data-table
                :headers="headers"
                :items="ventas"
                :search="search"
                :loading="loading"
                class="elevation-1"
                v-if="verNuevo==0"
            >
                <template v-slot:items="props">
                    <td class="justify-center layout px-0">
                        <v-icon
                        small
                        class="mr-2"
                        @click="verDetalles(props.item)"
                        >
                        tab
                        </v-icon>
                        <v-icon
                        small
                        class="mr-2"
                        @click="mostrarComprobante(props.item)"
                        >
                        print
                        </v-icon>
                        <template v-if="props.item.estado=='Aceptado'">
                            <v-icon
                            small
                            @click="activarDesactivarMostrar(2,props.item)"
                            >
                            block
                            </v-icon>
                        </template>
                    </td>
                    <td>{{ props.item.usuario }}</td>
                    <td>{{ props.item.cliente }}</td>
                    <td>{{ props.item.tipo_comprobante }}</td>
                    <td>{{ props.item.serie_comprobante }}</td>
                    <td>{{ props.item.num_comprobante }}</td>
                    <td>{{ props.item.fecha_hora }}</td>
                    <td>%{{ props.item.impuesto }}</td>
                    <td>${{ props.item.total }}</td>
                    <td>
                        <div v-if="props.item.estado=='Aceptado'">
                            <span class="blue--text">Aceptado</span>
                        </div>
                        <div v-else>
                            <span class="red--text">{{props.item.estado}}</span>
                        </div>
                    </td>
                
                </template>
                <template v-slot:no-data>
                <v-btn color="primary" @click="listar">Resetear</v-btn>
                </template>
            </v-data-table>
            <v-slide-y-transition mode="out-in">
            <template>
            <v-container grid-list-sm class="pa-4 white" v-if="verNuevo">
                <v-form ref="form">
                <v-layout row wrap >
                    
                        <v-flex xs12 sm4 md4 lg4 xl4>
                            <v-select v-model="tipo_comprobante"
                            :items="comprobantes" label="Tipo Comprobante" :rules="[v => !!v || 'Seleccione un Tipo Comprobante']" :readonly="readonly">
                            </v-select>
                        </v-flex>
                        <v-flex xs12 sm4 md4 lg4 xl4>
                            <v-text-field v-model="serie_comprobante" label="Serie Comprobante" maxlength="7" :rules="inputRules" :readonly="readonly">
                            </v-text-field>
                        </v-flex>
                        <v-flex xs12 sm4 md4 lg4 xl4>
                            <v-text-field v-model="num_comprobante" label="Nmero Comprobante" counter maxlength="10" :rules="inputRules" :readonly="readonly">
                            </v-text-field>
                        </v-flex>
                        <v-flex xs12 sm8 md8 lg8 xl8>
                            <v-select v-model="idcliente"
                            :items="clientes" label="Clientes" :rules="[v => !!v || 'Seleccione un Cliente']" :readonly="readonly">
                            </v-select>
                        </v-flex>
                        <v-flex xs12 sm4 md4 lg4 xl4>
                            <v-text-field type="number" prefix="%" v-model="impuesto" label="Impuesto" 
                            :rules="[v => !!v || 'El campo es requerido',
                                     v => v>0 ||'Ingrese un impuesto vlido']" :readonly="readonly">
                            </v-text-field>
                        </v-flex>
                        <v-flex xs12 sm8 md8 lg8 xl8>
                            <v-text-field v-if="verDet==0" v-model="codigo" @keyup.enter="buscarCodigo()" label="Cdigo" hint="Ingrese el cdigo del artculo y presione Enter">
                            </v-text-field>
                        </v-flex>
                    
                    <v-flex xs12 sm2 md2 lg2 xl2>
                        <v-btn v-if="verDet==0" @click="mostrarArticulos()" small fab dark color="teal">
                            <v-icon dark>list</v-icon>
                        </v-btn>
                    </v-flex>
                    <v-flex xs12 sm2 md2 lg2 xl2 v-if="errorArticulo">
                        <div class="red--text" v-text="errorArticulo">
                        </div>
                    </v-flex>
                    <v-flex xs12 sm12 md12 lg12 xl12>
                        <v-data-table
                            :headers="computedHeaders"
                            :items="detalles"
                            :loading="loading"
                            hide-actions
                            class="elevation-1"
                        >
                            <template v-slot:items="props">
                                <td v-if="verDet==0" class="justify-center layout px-0">
                                    <v-icon
                                    small
                                    class="mr-2"
                                    @click="eliminarDetalle(detalles,props.item)"
                                    >
                                    delete
                                    </v-icon>
                                </td>
                                <td>{{ props.item.articulo }}</td>
                                <td><v-text-field type="number" v-model="props.item.cantidad" :hint="verDet==0 ? props.item.stock+' en stock' : ''" 
                                :rules="[v => v<= props.item.stock ||'No sobrepase el stock de '+props.item.stock,
                                         v => v>0 ||'Ingrese un stock vlido']" :readonly="readonly"></v-text-field></td>
                                <td><v-text-field type="number" prefix="$" v-model="props.item.precio" :readonly="readonly"></v-text-field></td>
                                <td><v-text-field type="number" prefix="$" v-model="props.item.descuento" :readonly="readonly"></v-text-field></td>
                                <td>$ {{ (props.item.cantidad * props.item.precio -props.item.descuento).toFixed(2)}}</td>
                            </template>
                            <template v-slot:no-data>
                                <h3>No hay artculos agregados al detalle</h3>
                            </template>
                        </v-data-table>
                        <v-flex class="text-xs-right">
                            <strong>Total Parcial: </strong>$ {{totalParcial=(total-totalImpuesto).toFixed(2)}}
                        </v-flex>
                        <v-flex class="text-xs-right">
                            <strong>Total Impuesto: </strong>$ {{totalImpuesto=((total*impuesto)/(100+impuesto)).toFixed(2)}}
                        </v-flex>
                        <v-flex class="text-xs-right">
                            <strong>Total Neto: </strong>$ {{total=(calcularTotal).toFixed(2)}}
                        </v-flex>
                    </v-flex>
                    <v-flex xs12 sm12 md12 v-show="valida">
                        <div class="red--text" v-for="v in validaMensaje" :key="v" v-text="v">
                        </div>
                     </v-flex>
                    <v-flex xs12 sm12 md12 lg12 xl12>
                        <v-btn @click="ocultarNuevo" color="blue darken-1" flat>{{textoBtn}}</v-btn>
                        <v-btn v-if="verDet==0" @click="guardar" color="success" :loading="loadingBtn">Guardar</v-btn>
                    </v-flex>
                </v-layout>
                </v-form>
            </v-container>
            </template>
            </v-slide-y-transition>
        </v-flex>
    </v-layout>
</template>

<script>
    import axios from 'axios'
    import jsPDF from 'jspdf'
    import html2canvas from 'html2canvas'
    import moment from 'moment'
    export default {
        data(){
            return{

                ventas:[],
                dialog: false,
                headers: [
                { text: 'Opciones', value: 'opciones', sortable: false },
                { text: 'Usuario', value: 'usuario' },
                { text: 'Cliente', value: 'cliente' },
                { text: 'Tipo Comprobante', value: 'tipo_comprobante' },
                { text: 'Serie Comprobante', value: 'serie_comprobante',sortable: false},
                { text: 'Nmero Comprobante', value: 'num_comprobante',sortable: false},
                { text: 'Fecha', value: 'fecha_hora',sortable: false},
                { text: 'Impuesto', value: 'impuesto',sortable: false},
                { text: 'Total', value: 'total',sortable: false},
                { text: 'Estado', value: 'estado', sortable: false }
                
                ],
                cabeceraDetalles: [
                { text: 'Borrar', value: 'borrar', sortable: false },
                { text: 'Artculo', value: 'articulo', sortable: false },
                { text: 'Cantidad', value: 'cantidad',sortable: false},
                { text: 'Precio', value: 'precio',sortable: false},
                { text: 'Descuento', value: 'descuento',sortable: false},
                { text: 'Subtotal', value: 'subtotal', sortable: false }
                ],

                cabeceraArticulos:[
                    { text: 'Seleccionar', value: 'seleccionar', sortable: false },
                    { text: 'Artculo', value: 'articulo' },
                    { text: 'Categora', value: 'categoria'},
                    { text: 'Descripcin', value: 'descripcio',sortable: false},
                    { text: 'Stock', value: 'stock', sortable: false },
                    { text: 'Precio Venta', value: 'precio_venta',sortable: false},

                ],

                detalles:[],

                search: '',
                loading: true,
                loadingBtn: false,
                id: '',
                idcliente:'',
                clientes:[],
                tipo_comprobante:'',
                comprobantes:['FACTURA','BOLETA','TICKET'],
                serie_comprobante: '',
                num_comprobante: '',
                impuesto: 18,
                codigo:'',
                verNuevo:0,
                errorArticulo: null,
                totalParcial:0,
                totalImpuesto:0,
                total:0,
                articulos:[],
                texto:'',
                verArticulos:0,
                verDet:0,
                readonly:false,
                textoBtn: "Cancelar",
                valida: 0,
                validaMensaje:[],
                adModal: 0,
                adAccion: 0,
                adNombre: '',
                adId: '',
                inputRules:[
                     v => !!v || 'El campo es requerido',
                ],
                //Variables para imprimir Comprobante factura
                comprobanteModal:0,
                cliente:'',
                fecha_hora:'',
                num_documento:'',
                direccion:'',
                telefono:'',
                email:'',
                //
                hideBorrar:false

            }

        },
            computed: {
                calcularTotal:function(){
                    var resultado=0.0;
                    for (var i =0;i <this.detalles.length; i++) {
                        resultado=resultado+(this.detalles[i].precio*this.detalles[i].cantidad-this.detalles[i].descuento);
                    }
                    return resultado;
                },
                //Cdigo para ocultar header "Borrar" del la cabeceraDetalles al ver detalles
                computedHeaders () {
                    if(this.hideBorrar){
                        return this.cabeceraDetalles.filter(cabeceraDetalles => cabeceraDetalles.text !== "Borrar")
                    }
                    return this.cabeceraDetalles;
                }

            },

            watch: {
                dialog (val) {
                val || this.close()
                }
            },

            created () {
                this.listar();
                this.select();
            },
        methods:{
            crearPDF(){
                var quotes= document.getElementById('factura');
                html2canvas(quotes).then(function(canvas) {
                    var imgData= canvas.toDataURL('image/png');
                    var imgWidth = 210;
                    var pageHeight=295;
                    var imgHeight = canvas.height * imgWidth/ canvas.width;
                    var heightLeft = imgHeight;
                    var doc= new jsPDF('p', 'mm','a4');
                    var position=0;

                    doc.addImage(imgData, 'PNG',0,position,imgWidth,imgHeight);
                    doc.save('ComprobanteVenta.pdf');
                });
            },

            mostrarComprobante(item){
                this.limpiar();
                this.tipo_comprobante=item.tipo_comprobante;
                this.serie_comprobante=item.serie_comprobante;
                this.num_comprobante=item.num_comprobante;
                this.cliente=item.cliente;
                this.num_documento=item.num_documento;
                this.direccion=item.direccion;
                this.telefono=item.telefono;
                this.email=item.email;
                this.fecha_hora=item.fecha_hora;
                this.impuesto=item.impuesto;
                this.listarDetalles(item.idventa);
                this.comprobanteModal=1
            },
            ocultarComprobante(){
                this.comprobanteModal=0;
                this.limpiar();
            },
            mostrarNuevo(){
                this.verNuevo=1;
                this.hideBorrar=false;
            },
            ocultarNuevo(){
                this.verNuevo=0;
                this.limpiar();
            },

            buscarCodigo(){
                let me=this;
                me.errorArticulo=null;
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion={headers : header};
                axios.get('api/Articulos/BuscarCodigoVenta/'+this.codigo,configuracion).then(function(response){
                    //console.log(response);
                    me.agregarDetalle(response.data);
                    me.loading=false;

                }).catch(function(error){
                   // console.log(error);
                    me.errorArticulo='No existe el artculo';
                });

            },

            listarArticulo(){
                if (this.texto!='') {
                    let me=this;
                    let header={"Authorization" : "Bearer " + this.$store.state.token};
                    let configuracion={headers : header};
                    axios.get('api/Articulos/ListarVenta/'+me.texto,configuracion).then(function(response){
                        //console.log(response);
                        me.articulos=response.data;
                        //me.loading=false;

                    }).catch(function(error){
                        console.log(error);
                    });
                }
            },

            mostrarArticulos(){
                this.verArticulos=1;
            },
            ocultarArticulos(){
                this.verArticulos=0;
                this.articulos=[];
                this.texto='';
            },

            agregarDetalle(data=[]){
                this.errorArticulo=null;
                if (this.encuentra(data['idarticulo'])) {
                    this.errorArticulo="El artculo ya ha sido agregado";
                } else {
                    this.detalles.push(
                    {idarticulo:data['idarticulo'],
                    articulo: data['nombre'],
                    cantidad:1,
                    precio:data['precio_venta'],
                    descuento:0,
                    stock:data['stock']}
                  );
                  //console.log(this.detalles)
                  //var stockArticulo= data['stock'];
                  //this.hint[0]= 'hola '+ stockArticulo + "hola";
                  
                }
                
            },

            encuentra(id){
                var sw=0;
                for (var i = 0; i<this.detalles.length; i++) {
                    if (this.detalles[i].idarticulo==id) {
                        sw=1;
                    }
                }
                return sw;
            },

            eliminarDetalle(arr,item){
                var i= arr.indexOf(item);
                if (i!==-1) {
                    arr.splice(i,1);
                }
            },

            listar(){
                let me=this;
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion={headers : header};
                let url='';
                if (!me.search) {
                    url='api/Ventas/Listar';
                } else {
                    url='api/Ventas/ListarFiltro/'+me.search;
                }
                axios.get(url,configuracion).then(function(response){
                    //console.log(response);
                    me.ventas=response.data;
                    //Formateo de fecha y hora
                    me.ventas.map(function(x){
                        x.fecha_hora=moment(x.fecha_hora).format('DD/MM/YYYY HH:mm');
                    });
                    me.loading=false;

                }).catch(function(error){
                    console.log(error);
                });

            },

            
            listarDetalles(id){
                let me=this;
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion={headers : header};
                axios.get('api/Ventas/ListarDetalles/'+id,configuracion).then(function(response){
                    //console.log(response);
                    me.detalles=response.data;
                    me.loading=false;

                }).catch(function(error){
                    console.log(error);
                });

            },

            verDetalles(item){
                this.limpiar();
                this.tipo_comprobante=item.tipo_comprobante;
                this.serie_comprobante=item.serie_comprobante;
                this.num_comprobante=item.num_comprobante;
                this.idcliente=item.idcliente;
                this.impuesto=item.impuesto;
                this.listarDetalles(item.idventa);
                this.verNuevo=1;
                this.verDet=1;
                this.readonly=true;
                this.textoBtn="Volver";
                this.hideBorrar= true;
            },

            select(){
                let me=this;
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion={headers : header};
                var clientesArray=[];
                axios.get('api/Personas/SelectClientes',configuracion).then(function(response){
                    clientesArray=response.data;
                    clientesArray.map(function(x){
                        me.clientes.push({text: x.nombre, value:x.idpersona});
                    });

                }).catch(function(error){
                    console.log(error);
                });

            },

            limpiar(){
                this.id="";
                this.idcliente="";
                this.tipo_comprobante="";
                this.serie_comprobante="";
                this.num_comprobante="";
                this.impuesto=18;
                this.codigo="";
                this.detalles=[];
                this.total=0;
                this.totalImpuesto=0
                this.totalParcial=0;
                this.verDet=0;
                this.readonly=false;
                this.textoBtn="Cancelar";
                this.hideBorrar="false";
                this.loadingBtn=false;
                this.validaMensaje=[];
            },

            guardar () {

                    if (this.$refs.form.validate()) {
                        if (this.validar()) {
                            return;               
                        }

                        let header={"Authorization" : "Bearer " + this.$store.state.token};
                        let configuracion={headers : header};
                            //Cdigo para guardar
                            let me=this;
                            me.loadingBtn=true;
                            axios.post('api/Ventas/Crear',{
                                'idcliente':me.idcliente,
                                'idusuario':me.$store.state.usuario.idusuario,
                                'tipo_comprobante': me.tipo_comprobante,
                                'serie_comprobante': me.serie_comprobante,
                                'num_comprobante': me.num_comprobante,
                                'impuesto': me.impuesto,
                                'total': me.total,
                                'detalles':me.detalles
                            },configuracion).then(function(response){
                                me.ocultarNuevo();
                                me.listar();
                                me.limpiar();
                                me.$emit('registroGuardado');
                            }).catch(function(error){
                                console.log(error);
                            });     
                        
                    }
                },

                validar(){
                this.valida=0;
                this.validaMensaje=[];
                
                if (this.detalles.length<=0) {
                    this.validaMensaje.push("Ingrese al menos un artculo al detalle.");
                }

                if(this.validaMensaje.length){
                    this.valida=1;
                }

                return this.valida;

            },

            activarDesactivarMostrar(accion,item){
                this.adModal=1;
                this.adNombre= item.num_comprobante;
                this.adId=item.idventa;


                if (accion==1) {
                    this.adAccion=1;
                }
                else if(accion=2){
                    this.adAccion=2;

                }
                else{
                    this.adModal=0;
                }
            },

            activarDesactivarCerrar(){
                     this.adModal=0;
            },
            /*
            activar(){
                    let me=this;
                    let header={"Authorization" : "Bearer " + this.$store.state.token};
                    let configuracion={headers : header};
                            axios.put('api/Usuarios/Activar/'+this.adId,{},configuracion).then(function(response){
                                me.adModal=0;
                                me.adAccion=0;
                                me.adNombre="";
                                me.adId="";
                                me.listar();
                            }).catch(function(error){
                                console.log(error);
                            });
            },
            */
            desactivar(){
                    let me=this;
                    let header={"Authorization" : "Bearer " + this.$store.state.token};
                    let configuracion={headers : header};
                            axios.put('api/Ventas/Anular/'+this.adId,{},configuracion).then(function(response){
                                me.adModal=0;
                                me.adAccion=0;
                                me.adNombre="";
                                me.adId="";
                                me.listar();
                            }).catch(function(error){
                                console.log(error);
                            });

            }

        }
            
    }
</script>
